const Package = ({ onHandleOpenPackage, packageType }) => {
  if (packageType === 'common') {
    return <CommonOpeningCard onHandleOpenPackage={onHandleOpenPackage} />
  }

  if (packageType === 'rare') {
    return <RareOpeningCard onHandleOpenPackage={onHandleOpenPackage} />
  }

  return <EpicOpeningCard onHandleOpenPackage={onHandleOpenPackage} />
}

const defaultPages = [
  ({ style, onHandleOpenPackage, packageType }) => (
    <animated.div style={{ ...style }}>
      <Package onHandleOpenPackage={onHandleOpenPackage} packageType={packageType} />
    </animated.div>
  ),
]

const NextArrow = ({ className, style, onClick, index, disabled }) => {
  const IMAGE_MAP = {
    0: '/images/arrow_common_right.png',
    1: '/images/arrow_rare_right.png',
    2: '/images/arrow_epic_right.png',
  }

  return (
    <ForwardIcon
      className={className}
      onClick={() => {
        if (!disabled) {
          onClick()
        }
      }}
      src="/images/arrow_common_right.png"
      style={{
        ...style,
      }}
    />
  )
}

const BackArrow = ({ className, style, onClick, index, disabled }) => {
  const IMAGE_MAP = {
    0: '/images/arrow_common_left.png',
    1: '/images/arrow_rare_left.png',
    2: '/images/arrow_epic_left.png',
  }
  return (
    <BackIcon
      src="/images/arrow_common_left.png"
      className={className}
      onClick={() => {
        if (!disabled) {
          onClick()
        }
      }}
      style={{
        ...style,
      }}
    />
  )
}

const settings = {
  dots: false,
  infinite: true,
  fade: true,
  draggable: false,
  swipe: false,
  speed: 500,
  cssEase: 'cubic-bezier(0.165, 0.840, 0.440, 1.000)',
  slidesToShow: 1,
  slidesToScroll: 1,
  nextArrow: <NextArrow index={0} />,
  prevArrow: <BackArrow index={0} />,
  beforeChange: (current, next) => {},
}
const DECK_PAGES = [
  ({ style }) => (
    <animated.div style={{ ...style }}>
      <SliderWrapper>
        <Slider {...settings}>
          <FlipCard />
          <FlipCard />
          <FlipCard />
          <FlipCard />
          <FlipCard />
        </Slider>
      </SliderWrapper>
      <CardText fontSize="2.6rem">Cybloc NFT 1/3</CardText>
    </animated.div>
  ),
]

const PackageOpening = ({ packageData }) => {
  const [pages, setPages] = useState([])
  const [step, setStep] = useState(1)
  const { onUnpack } = useOpenPackage()
  const location = useLocation()
  const { packageType } = useParams()
  const packageIndexes = useMemo(() => {
    const queriesObj = queryString.parse(location.search, { arrayFormat: 'bracket' })
    if (queriesObj.packageIndex) {
      return queriesObj.packageIndex
    }

    return []
  }, [location.search])

  useEffect(() => {
    const CARD_LIST = packageIndexes.map((i) => {
      if (packageType === 'common') {
        return <CommonOpeningCard />
      }

      if (packageType === 'rare') {
        return <RareOpeningCard />
      }

      return <EpicOpeningCard />
    })

    setPages([
      ({ style }) => (
        <animated.div style={{ ...style }}>
          <Deck
            cards={CARD_LIST}
            isDraggable={false}
            isStacked
            cardContainerStyle={{
              top: '0px',
            }}
          />
        </animated.div>
      ),
    ])
  }, [])

  const transRef = useSpringRef()
  /* Config for transition */
  const transitions = useTransition(pages, {
    ref: transRef,
    keys: null,
    from: { opacity: 0, transform: 'translateY(-100px)' },
    enter: (item) => async (next, cancel) => {
      await next({ opacity: 1, position: 'relative', zIndex: 2 })
      await next({ transform: 'translateY(0px)' })
    },
    leave: (item) => async (next, cancel) => {
      await next({ opacity: 0, transform: 'translateY(-100px)' })
    },
    config: { duration: 500 },
    expires: true,
    trail: 2000,
  })

  const onHandleOpenPackage = async (indexes) => {
    // const packages = indexes.map((index) => {
    //   return [
    //     Number(index),
    //     [
    //       [1, 2, 2, 1, '120398102938019238098'],
    //       [1, 0, 1, 1, '838293481723'],
    //       [1, 1, 0, 1, '584930802374'],
    //     ],
    //   ]
    // })
    // setPages((state) => [({ style }) => <LoaderIcon />])
    // const result = await onUnpack(packages)
    setPages((state) => [
      ({ style }) => (
        <animated.div style={{ ...style }}>
          <Flex mb="50px">
            <PackageFrame url="/images/frame-opening.png">
              <Flex
                flexDirection="column"
                justifyContent="center"
                pt="25px"
                pb="25px"
                height="100%"
                alignItems="center"
              >
                <StyledPackText>{1}</StyledPackText>
                <PackageImage src="/images/common.png" />
              </Flex>
            </PackageFrame>
          </Flex>
        </animated.div>
      ),
    ])
  }

  useEffect(() => {
    transRef.start()
  }, [pages])

  return (
    <Wrapper>
      <Row>
        <UnionImage src="/images/Union.svg" />
        <UnionImage2 src="/images/Union.svg" />
        <TransitionWrapper>
          {transitions((style, Page) => {
            return (
              <animated.div style={{ ...style }}>
                <Page
                  packageType={packageType}
                  onHandleOpenPackage={() => {
                    onHandleOpenPackage()
                  }}
                />
              </animated.div>
            )
          })}
        </TransitionWrapper>
        <ActionButton
          onClick={() => {
            setPages(DECK_PAGES)
            setStep(2)
          }}
          step={step}
        />
      </Row>
    </Wrapper>
  )
}

export default PackageOpening
