import React from 'react'
import TimerBlock from './TimerBlock'
import { Wrapper, StyledHeaderText, StyledHeaderTextContent, BackgroundImage } from './styles'

import { OpenAtTimestamp } from '../../config/constant'

const Countdown = () => {
  const numberSeconds = OpenAtTimestamp - Math.floor(Date.now() / 1000)

  return (
    <Wrapper>
      <BackgroundImage>
        <StyledHeaderText top="74px">Genesis Drop</StyledHeaderText>
        <StyledHeaderTextContent top="23px">Tuesday, October 19th 1:00 PM UTC</StyledHeaderTextContent>
        <TimerBlock openAtSeconds={numberSeconds} />
      </BackgroundImage>
    </Wrapper>
  )
}

export default Countdown
