import { useState } from 'react'
import styled from 'styled-components'
import Box from 'components/Box'

const FlipCardWrapper = styled(Box)`
  width: 300px;
  height: 400px;
  perspective: 1000px;
  margin: 30px auto;
  &:hover {
    cursor: pointer;
  }
`
const FlipCardInner = styled(Box)`
  z-index: 10;
  backface-visibility: visible;
  transform-style: preserve-3d;
  transition: 0.5s linear 0.1s;
  position: relative;
  width: inherit;
  height: inherit;
  transform: ${(props) => props.showBack && 'rotateY(180deg)'};
`

const Card = styled(Box)`
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  border: 0;
`

const CardFront = styled(Card)`
  z-index: 9;
  transform: rotateY(180deg);
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
`

const CardBack = styled(Card)`
  transform: rotateY(0);
  z-index: 8;
  background-color: #fff;
`
const Image = styled.img`
  width: 100%;
  height: 100%;
`

const FlipCard = () => {
  const [showBack, setShowBack] = useState(false)

  const handleClick = () => {
    setShowBack(true)
  }

  return (
    <FlipCardWrapper onClick={handleClick}>
      <FlipCardInner showBack={showBack}>
        <CardFront>
          <Image src="/images/nft-detail.png" alt="card-front" />
        </CardFront>
        <CardBack>
          <Image src="/images/card-back.png" alt="card-back" />
        </CardBack>
      </FlipCardInner>
    </FlipCardWrapper>
  )
}

export default FlipCard
