import React, { useState } from 'react'
import { useSprings, animated, to as interpolate } from '@react-spring/web'
import Flex from 'components/Flex'
import styles from './styles.module.scss'

// These two are just helpers, they curate spring data, values that are later being interpolated into css
const to = (i) => ({ x: 0, y: i * -4, scale: 1, rot: -10 + Math.random() * 20, delay: i * 100 })
const toForDeck = (i) => ({ x: -(i * 20), y: i * 15, scale: 1, delay: i * 100 })
const from = (i) => ({ x: 0, rot: 0, scale: 1.5, y: -1000 })
// This is being used down there in the view, it interpolates rotation and scale into a css transform

function Deck({ cards, cardContainerStyle = {}, isDraggable = true, isStacked = false }) {
  const [props, api] = useSprings(cards.length, (i) =>
    isStacked ? { ...toForDeck(i), from: from(i) } : { ...to(i), from: from(i) },
  )
  return (
    <Flex
      justifyContent="center"
      style={{
        position: 'relative',
      }}
    >
      {props.map(({ x, y, rot, scale }, i) => (
        <animated.div className={styles.deck} key={i} style={{ x, y, ...cardContainerStyle }}>
          {/* This is the card itself, we're binding our gesture to it (and inject its index so we know which is which) */}
          <animated.div style={!isStacked ? { transform: interpolate([rot, scale]) } : {}}>{cards[i]}</animated.div>
        </animated.div>
      ))}
    </Flex>
  )
}

export default Deck
