import React, { useState } from 'react'
import styled from 'styled-components'
import queryString from 'query-string'
import { useHistory, useRouteMatch } from 'react-router-dom'
import { CSSTransition, SwitchTransition } from 'react-transition-group'
import { useModal, CardDetailModal, OpenPackModal } from 'components/Modal'
import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'
import PageLoading from 'components/PageLoading'
import { CommonPackageCard, RarePackageCard, EpicPackageCard } from './components/PackageCard'
import { CommonCyblocCard, RareCyblocCard } from './components/CyblocCard'
import NFTDetail from './NFTDetail'

const Wrapper = styled(Box)`
  background-color: #191123;
  min-height: inherit;
  padding-bottom: 45px;
`
const Row = styled(Box)`
  max-width: 1000px;
  margin: 0 auto;

  @media (max-width: 1100px) {
    max-width: 800px;
  }

  @media (max-width: 600px) {
    max-width: 400px;
  }
`
const StyledHeaderContent = styled(Flex)`
  justify-content: space-between;
  align-items: center;

  @media (max-width: 600px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 40px;
  }
`

const StyledHeaderText = styled(Text)`
  font-size: ${(props) => (props.isActive ? '3rem' : '1.4rem')};
  line-height: 48px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #f3f3f3;
  cursor: pointer;
  padding: 30px 0px;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  transition: 0.3s opacity ease-out;

  @media (max-width: 600px) {
    padding: 0px;
  }
`
const OpenPackButton = styled.button`
  justify-content: center;
  align-items: center;
  display: flex;
  background-color: transparent;
  border: none;
  background-image: url('/images/open-pack-frame.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in, opacity: 0.3s ease-in;
  padding: 15px 20px;
  border-radius: 2px;
  opacity: ${(props) => (props.disabled ? '0.2' : '1')};

  &:hover {
    cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
    opacity: ${(props) => (props.disabled ? '0.2' : '1')};
  }

  &:active:enabled {
    transform: translateY(-2px);
    opacity: 80%;
  }

  &:not(:last-of-type) {
    margin-right: 20px;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`
const StyledCardWrapper = styled(Flex)`
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: 1100px) {
    justify-content: center;
  }
`

const Inventory = ({ packageData, isLoading, cyblocData }) => {
  const { commonCybloc, rareCybloc, epicCybloc } = cyblocData
  const [tabIndex, setTabIndex] = useState(1)
  const history = useHistory()
  const [selectedPackage, setSelectedPackage] = useState({
    type: null,
    packageIndex: [],
  })
  const { path } = useRouteMatch()
  const [onPresentOpenPackModal] = useModal(<OpenPackModal packageData={packageData} />)
  const [onPresentViewCyBlocDetailModal] = useModal(
    <CardDetailModal>
      <NFTDetail />
    </CardDetailModal>,
  )
  const { commonPackageData, rarePackageData, epicPackageData } = packageData

  const onNavigateToOpen = (packageType, packageIndex) => {
    const stringtifiedQuery = queryString.stringify(
      {
        packageIndex,
      },
      {
        arrayFormat: 'bracket',
      },
    )
    history.push(`${path}/package-opening/${packageType}?${stringtifiedQuery}`)
  }

  const onNavigateToNFTDetail = () => {
    history.push(`${path}/nft-detail`)
  }

  const onHandleSelectPackage = (packageType, index) => {
    const { type, packageIndex } = selectedPackage
    if (type === packageType) {
      if (packageIndex.includes(index)) {
        setSelectedPackage({
          ...selectedPackage,
          packageIndex: packageIndex.filter((i) => i !== index),
        })
      } else {
        setSelectedPackage({
          ...selectedPackage,
          packageIndex: [...packageIndex, index],
        })
      }
    } else {
      setSelectedPackage({
        type: packageType,
        packageIndex: [index],
      })
    }
  }

  return (
    <Wrapper>
      <Row>
        {isLoading ? (
          <PageLoading />
        ) : (
          <>
            <StyledHeaderContent>
              <Flex alignItems="baseline">
                <StyledHeaderText mr="15px" isActive={tabIndex === 1} onClick={() => setTabIndex(1)}>
                  Packs
                </StyledHeaderText>
                <StyledHeaderText isActive={tabIndex === 2} onClick={() => setTabIndex(2)}>
                  Cyblocs
                </StyledHeaderText>
              </Flex>
              <CSSTransition in={tabIndex === 1} timeout={200} classNames="fade" unmountOnExit>
                <Flex>
                  <OpenPackButton
                    disabled={selectedPackage.packageIndex.length === 0}
                    onClick={() => {
                      const { type, packageIndex } = selectedPackage
                      onNavigateToOpen(type, packageIndex)
                    }}
                  >
                    <Text>Open selected packs</Text>
                  </OpenPackButton>
                  <OpenPackButton onClick={onPresentOpenPackModal}>
                    <Text>Open multiple packs</Text>
                  </OpenPackButton>
                </Flex>
              </CSSTransition>
            </StyledHeaderContent>
            <SwitchTransition mode="out-in">
              <CSSTransition
                key={tabIndex}
                addEndListener={(node, done) => {
                  node.addEventListener('transitionend', done, false)
                }}
                classNames="fade"
              >
                {tabIndex === 1 ? (
                  <StyledCardWrapper>
                    {commonPackageData.map((data) => {
                      return (
                        <CommonPackageCard
                          data={data}
                          onClick={() => onHandleSelectPackage('common', data)}
                          selectedPackage={selectedPackage}
                        />
                      )
                    })}
                    {rarePackageData.map((data) => {
                      return (
                        <RarePackageCard
                          data={data}
                          onClick={() => onHandleSelectPackage('rare', data)}
                          selectedPackage={selectedPackage}
                        />
                      )
                    })}
                    {epicPackageData.map((data) => {
                      return (
                        <EpicPackageCard
                          data={data}
                          onClick={() => onHandleSelectPackage('epic', data)}
                          selectedPackage={selectedPackage}
                        />
                      )
                    })}
                  </StyledCardWrapper>
                ) : (
                  <Flex flexWrap="wrap" justifyContent="center" mt="24px">
                    {new Array(commonCybloc).fill(0).map((data) => {
                      return <CommonCyblocCard onClick={onNavigateToNFTDetail} />
                    })}
                    {new Array(rareCybloc).fill(0).map((data) => {
                      return <RareCyblocCard onClick={onNavigateToNFTDetail} />
                    })}
                    <CommonCyblocCard onClick={onPresentViewCyBlocDetailModal} />
                  </Flex>
                )}
              </CSSTransition>
            </SwitchTransition>
          </>
        )}
      </Row>
    </Wrapper>
  )
}

export default Inventory
